import { mapState } from "vuex";

export default {
    data() {
        return {
            loadingOver: false,
            list: []
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            bonusImg: state => state.mallConfig.__wxapp_img.bonus
        })
    },
    methods: {
        getList() {
            this.$request({
                url: this.$api.bonus.member,
            }).then(response=>{
                this.$hideLoading();
                if(response.code === 0) {
                    this.list = response.data.list;
                    this.loadingOver = true;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getList();
    }
}
